const API_KEY = "398e1a63069b2e5454b3bb55b99f1816";
const BASE_PATH = `https://api.themoviedb.org/3`;
export function getMoive() {
  return fetch(`${BASE_PATH}/movie/now_playing?api_key=${API_KEY}&language=ko`).then(ele => ele.json());
}
interface Movie {
  id: number;
  backdrop_path: string;
  original_language: string;
  overview: string;
  poster_path: string;
  title: string;
}
export interface GetMoiveResult {
  data: {
    maximum: string;
    minimum: string;
  };
  page: number;
  total_pages: number;
  total_results: number;
  results: Movie[];
}
export interface SearchResult {
  page: number;
  total_pages: number;
  total_results: number;
  results: SerachArray[];
}
interface SerachArray {
  id: number;
  name: string;
  poster_path: string;
  original_title: string;
  backdrop_path: string;
  overview: string;
  title: string;
}
export function searchInfo(keyword: string | null) {
  return fetch(`${BASE_PATH}/search/multi?api_key=${API_KEY}&language=ko&query=${keyword}&page=${1}`).then(ele => ele.json());
}
